.toggleSection {
    display: none;
}

.navContent {
    display: block;
}

.toggle_btn {
    text-decoration: none;
    color: black;
}

.offcanvas-end {
    width: 260px;
}

.cover-spin {
    position: fixed;
    width: 100%;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    background-color: rgba(255, 255, 255, 0.7);
    z-index: 9999;

}

@media (max-width: 995px) {
    .toggleSection {
        display: block;
    }

    .navContent {
        display: none;
    }
}